import request from '@/utils/request'

export default {
  // 获取首页banner轮播图数据
  getBannerList() {
    return request({
      url: '/educms/userBanner/getBannerLimitThree',
      method: 'get'
    })
  },
  // 获取前台首页热门课程以及名师数据
  getIndexHotData() {
    return request({
      url: '/eduService/front/index',
      method: 'get'
    })
  }
}